.switch {
  width: 40px;
  height: 20px;
  margin: 4px;
  display: inline-block;
  position: relative;
  transform: translateY(15%);
}

.slider {
  box-shadow: 0 0 0 1px var(--main-text-cl), 0 0 2px var(--main-text-cl);
  cursor: pointer;
  border: 3px solid rgba(0, 0, 0, 0);
  border-radius: 30px;
  transition: all .2s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.dark .slider {
  background-color: var(--main-text-cl);
  box-shadow: 0 0 0 1px var(--white-cl), 0 0 2px var(--white-cl);
  overflow: hidden;
}

.slider:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: var(--main-text-cl);
  border-radius: 30px;
  transition: all .2s;
  position: absolute;
  transform: translateX(-20px);
}

.dark .slider:before {
  background-color: var(--white-cl);
}

input:checked + .slider:before {
  background-color: var(--main-text-cl);
  transform: translateX(20px);
}

.dark input:checked + .slider:before {
  background-color: var(--white-cl);
}

