  color: #000;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: color .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 5px;
  right: 5px;
}

@media screen and (min-width: 768px) {
  .modal-team__close-btn {
    top: 10px;
    right: 10px;
  }
}

.modal-team__close-btn:hover {
  color: #ff6b01;
}

.modal-team__close-icon {
  fill: currentColor;
}

.team-title-wrapper {
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .team-title-wrapper {
    padding-left: 50px;
  }
}

@media screen and (min-width: 1280px) {
  .team-title-wrapper {
    justify-content: center;
    padding-left: 0;
  }
}

.team-logo {
  width: 100px;
  height: 35px;
  background-color: rgba(0, 0, 0, 0);
  background-position: -10%, 10%;
  background-repeat: no-repeat;
  background-size: 100%;
  display: inline-block;
}

@media screen and (min-width: 768px) {
  .team-logo {
    width: 140px;
    height: 48px;
  }
}

.team__name {
  text-align: center;
  text-transform: uppercase;
  margin-left: -17px;
  font-size: 24px;
  text-decoration: underline #ff6b08;
}

@media screen and (min-width: 768px) {
  .team__name {
    margin-left: -20px;
    font-size: 32px;
  }
}

.team {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.2rem;
  display: flex;
}

.team__item {
  border-radius: 10px;
  padding-bottom: 5px;
  transition: box-shadow .5s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
}

.team__item:hover, .team__item:focus {
  box-shadow: 0 16px 32px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .22);
}

.img-wrapper {
  overflow: hidden;
}

.team__img {
  object-fit: cover;
  width: 120px;
  height: 120px;
  border-radius: 10px 10px 0 0;
  margin-bottom: 5px;
  transition: transform .8s cubic-bezier(.4, 0, .2, 1);
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .team__img {
    width: 150px;
    height: 150px;
  }
}

.team__item:hover .team__img, .team__item:focus .team__img {
  transform: translate(-7%, 12%)scale(1.1);
}

.team__link {
  color: #000;
}

.team__icon {
  fill: currentColor;
  transition: transform .5s ease-in;
  position: absolute;
}

.team__icon:hover, .team__icon:focus {
  transform: scale(1.7);
}

.team__icon--linkedin {
  bottom: 15px;
  left: 13px;
}

@media screen and (max-width: 767px) {
  .team__icon--linkedin {
    bottom: 15px;
    left: 7px;
  }
}

.team__icon--github {
  fill: #000;
  bottom: 15px;
  right: 13px;
}

@media screen and (max-width: 767px) {
  .team__icon--github {
    bottom: 15px;
    right: 7px;
  }
}

.team__text-content {
  text-align: center;
  font-size: 11px;
  font-weight: 500;
}

@media screen and (min-width: 768px) {
  .team__text-content {
    font-size: 13px;
  }
}

.team__member {
  margin-bottom: 3px;
  font-size: 13px;
}

@media screen and (min-width: 768px) {
  .team__member {
    margin-bottom: 5px;
    font-size: 16px;
  }
}

.team__role {
  color: #ff6b08;
}

